















import { Component, Vue } from 'vue-property-decorator'
import { base } from '@/store'
import { Area, UserAction } from '../@types/types'
import { ArchetypeColor, Color } from '../@types/archetype'

@Component
export default class Conclusion extends Vue {
  get pageConfig() {
    return base.currentPageConfig
  }
  get area() {
    return this.pageConfig && this.pageConfig.area
  }
  get topArchetype() {
    if (this.area && base.currentState) {
      switch (this.area) {
        case Area.persona:
          return base.summary.top_archetypes.persona
        case Area.shadow:
          return base.summary.top_archetypes.shadow
        case Area.self:
          return base.summary.top_archetypes.self
      }
    }
    return null
  }
  next() {
    // submit to state history
    base.submitAction({
      type: UserAction.press_next,
    })
  }
  mounted() {
    let color = 'white'
    if (this.area != 'shadow') {
      if (this.topArchetype) {
        color = Color[ArchetypeColor[this.topArchetype]]
      }
    } else {
      color = 'black'
    }
    base.setBackgroundColor(color)
  }
}
