





















import { Component, Vue } from 'vue-property-decorator'
import { base } from '@/store'
import { Archetype } from '../@types/archetype'
import {
  PersonalityPart,
  UserAction,
} from '../@types/types'

@Component
export default class ShadowAnswers extends Vue {
  imgs: Record<Archetype, string> = {
    Ca: require('@/assets/shadow/Ca.png'),
    Ev: require('@/assets/shadow/Ev.png'),
    In: require('@/assets/shadow/In.png'),
    He: require('@/assets/shadow/He.png'),
    Ex: require('@/assets/shadow/Ex.png'),
    Re: require('@/assets/shadow/Re.png'),
    Lo: require('@/assets/shadow/Lo.png'),
    Cr: require('@/assets/shadow/Cr.png'),
    En: require('@/assets/shadow/En.png'),
    Sa: require('@/assets/shadow/Sa.png'),
    Ma: require('@/assets/shadow/Ma.png'),
    Ru: require('@/assets/shadow/Ru.png'),
  }

  selected: Archetype | null = null

  get archetypes(): string[] {
    return Object.keys(Archetype)
  }

  select(arch: Archetype) {
    if (this.selected !== arch) this.selected = arch
    else this.selected = null
  }

  next() {
    if (this.selected) {
      base.submitAction({
        type: UserAction.answer_question,
        info: this.selected,
        changes: [
          [PersonalityPart.shadow, [this.selected, 0]],
        ],
      })
    }
  }
  mounted() {
    base.setBackgroundColor('white')
  }
}
