


















import {
  Component,
  Vue,
  Prop,
} from 'vue-property-decorator'

import { IAnswer, questions } from '../@types/archetype'

@Component
export default class AnswerSet extends Vue {
  @Prop({ type: Array, default: () => [] })
  answers!: IAnswer[]

  @Prop({ type: Function, default: () => {} })
  pickAnswer!: (answer: IAnswer, idx: number) => void

  questions = questions
}
