



































import { Component, Vue } from 'vue-property-decorator'
import { base } from '@/store'
import { Gender, UserAction } from '@/@types/types'

@Component
export default class Welcome extends Vue {
  re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  email: string | null = null
  name: string | null = null
  age: number = 0
  gender: Gender | null = null
  domicile: string | null = null
  info_from: string | null = null

  submit() {
    // submit to summary
    // submit to state history
    base.submitAction({
      type: UserAction.submit_form,
    })
  }

  get emailIsValid(): boolean {
    if (this.email) {
      return this.re.test(this.email.toLowerCase())
    }
    return false
  }

  mounted() {
    base.setBackgroundColor('white')
  }
}
