




















import {
  Component,
  Vue,
  Prop,
} from 'vue-property-decorator'
import {
  Archetype,
  Color,
  ArchetypeColor,
} from '../@types/archetype'

@Component
export default class GraphLabel extends Vue {
  @Prop({ type: Boolean, default: false })
  selected!: boolean

  @Prop({ type: String, default: Archetype.Ev })
  arch!: Archetype

  @Prop({ type: String, default: Color['Biru Muda'] })
  color!: Archetype
}
