import Vue from 'vue'
import Vuex from 'vuex'
import { getModule } from 'vuex-module-decorators'

Vue.use(Vuex)

import { getTopKey } from '@/utils'

import {
  VuexModule,
  Module,
  Action,
  Mutation,
} from 'vuex-module-decorators'

import {
  Page,
  Summary,
  IPageConfig,
  State,
  UserAction,
  PersonalityPart,
  UserData,
  Change,
} from '@/@types/types'

import { Archetype } from '@/@types/archetype'

import {
  pageOrder,
  pageConfigs,
} from '@/configs/pageConfig'

import axios from 'axios'

@Module({
  name: 'base',
  namespaced: true,
})
class Base extends VuexModule {
  // dynamic vars
  stateHistory: State[] = [new State()]
  summary = new Summary()
  currentState = new State()

  backgroundColor: string = 'white'
  @Mutation public setBackgroundColor(color: string) {
    this.backgroundColor = color
  }

  // get currentState(): State | null {
  //   console.log(this.stateHistory.length)
  //   console.log(
  //     this.stateHistory[this.stateHistory.length - 1]
  //   )

  //   if (this.stateHistory.length)
  //     return this.stateHistory[this.stateHistory.length - 1]
  //   return null
  // }
  get currentPageIdx(): number {
    if (this.currentState.page)
      return Math.min(
        pageOrder.length - 1,
        pageOrder.indexOf(this.currentState.page) + 1
      )
    return 0
  }
  get currentPageConfig(): IPageConfig {
    return pageConfigs[pageOrder[this.currentPageIdx]]
  }

  // actions and mutations
  @Action({ rawError: true }) public submitAction(payload: {
    type: UserAction
    info?: string
    page?: Page | null
    changes?: Change[]
  }) {
    // create new state based on old and changes
    const newState = Object.assign({}, this.currentState)

    newState.session_id = this.summary.session_id
    newState.email = this.summary.user_data.email

    newState.action_type = payload.type
    newState.action_info = payload.info

    const date = new Date()
    newState.date = date.toLocaleDateString()
    newState.time = date.toLocaleTimeString()

    if (payload.page !== undefined)
      newState.page = payload.page
    else newState.page = this.currentPageConfig.page

    newState.in_free_mode = this.freeMode

    let personaChanged = false
    payload.changes &&
      payload.changes.map(change => {
        // persona
        if (change[0] == PersonalityPart.persona) {
          newState.persona_scores[change[1][0]] =
            change[1][1]
          personaChanged = true
        }
        // shadow
        else if (change[0] == PersonalityPart.shadow) {
          newState.top_archetypes.shadow = change[1][0]
        }
        // self
        else if (change[0] == PersonalityPart.self) {
          newState.top_archetypes.self = change[1][0]
        }
      })

    // get top persona archetypes
    if (personaChanged)
      newState.top_archetypes.persona = getTopKey(
        newState.persona_scores
      )!

    // mutate state
    this.mSubmitAction(newState)

    // upload state

    // axios
    //   .post(
    //     'http://tujusemesta.com/ourchetypes/data/test.php',
    //     {
    //       todo: 'Buy the milk',
    //     }
    //   )
    //   .then(res => {
    //     console.log(`statusCode: ${res.statusCode}`)
    //     console.log(res)
    //   })
    //   .catch(error => {
    //     console.error(error)
    // })
  }
  @Mutation private mSubmitAction(state: State) {
    this.stateHistory.push(state)
    this.currentState = state

    // also mutate summary
    this.summary.persona_scores = this.currentState.persona_scores
    this.summary.top_archetypes = this.currentState.top_archetypes
  }

  @Action({ rawError: true }) public submitForm(
    data: UserData
  ) {
    this.mSubmitForm(data)
  }
  @Mutation private mSubmitForm(data: UserData) {
    const date = new Date()

    // set session id from start datetime
    this.summary.session_id = date.toISOString()

    // set start times
    this.summary.start_date = date.toLocaleDateString()
    this.summary.start_time = date.toLocaleTimeString()

    // set the rest
    this.summary.user_data = data
  }

  @Mutation public endRun() {
    const date = new Date()
    this.summary.end_date = date.toLocaleDateString()
    this.summary.end_time = date.toLocaleTimeString()

    if (this.freeMode)
      this.summary.in_free_mode = this.freeMode

    // upload summary
  }

  // Free testing stuff
  public freeMode: boolean = false
  @Action({ rawError: true }) public freeSkip(vals: {
    page?: Page | null
    persona?: Archetype
    shadow?: Archetype
    self?: Archetype
  }) {
    // set free mode
    this.setFreeMode(true)

    // change demo data
    const demoData = new UserData()
    demoData.email = '--testing--'
    this.mSubmitForm(demoData)

    // add action
    const changes: [
      PersonalityPart,
      [Archetype, number]
    ][] = []
    if (vals.persona)
      changes.push([
        PersonalityPart.persona,
        [vals.persona, 1000],
      ])
    if (vals.shadow)
      changes.push([
        PersonalityPart.shadow,
        [vals.shadow, 1000],
      ])
    if (vals.self)
      changes.push([
        PersonalityPart.self,
        [vals.self, 1000],
      ])

    if (vals.page) {
      let idx = pageOrder.indexOf(vals.page) - 1
      vals.page = pageOrder[idx]
      // if undefined, set to null to be handled later
      if (!vals.page) vals.page = null
    }

    this.submitAction({
      type: UserAction.free_skipping,
      info: '',
      page: vals.page,
      changes,
    })
  }
  @Mutation private setFreeMode(isFree: boolean) {
    this.freeMode = isFree
  }
}

const store = new Vuex.Store({
  modules: {
    base: Base,
  },
})

export default store

export const base = getModule(Base, store)
